*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
  cursor: default;
}

body{
  height: 100vh;
  background: #8E2DE2;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #4A00E0, #8E2DE2);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #4A00E0, #8E2DE2); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.App{
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  position: absolute;
  left: 50%;
  top: 47%;
  transform: translate(-50%, -50%);
  width: 100%;
}

.title{
  color: white;
  font-size: 70px;
  margin-bottom: 20px;
  font-family: 'Poppins', sans-serif;
}

.board{
  display: grid;
  grid-template-columns: 190px 190px 190px;
  gap: 10px;
}

.square{
  width: 190px;
  height: 190px;
  background-color: white;
  border: none;
  border-radius: 10px;
  text-align: center;
  line-height: 190px;
  font-size: 100px;
  color: #4A00E0;
  font-weight: 600;
  cursor: pointer;
}

.gameover{
  background-color: rgba(0, 0, 0, 0.90);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

 .gameover > h1 {
  color: white;
  font-family: 'Poppins', sans-serif;
  font-size: 70px;
 }

 .gameover > h1.winner{
  color: rgb(0, 174, 255);
 }

.gameover-button{
  margin-top: 80px;
  width: 250px;
  height: 60px;
  background-color: rgb(255, 202, 26);
  color: white;
  font-weight: 800;
  font-size: 25px;
  border: none;
  text-shadow: 2px 0px 4px black;
}

.gameover-button:hover{
  cursor: pointer;
  background-color: rgb(155, 119, 0);
}

@media (max-width: 600px){

  .title{
    font-size: 50px;
  }

  .board{
    grid-template-columns: 90px 90px 90px;
    font-size: 90px;
  }

  .square{
    width: 90px;
    height: 90px;
    line-height: 90px;
    font-size: 45px;
  }

  .gameover-button{
    margin-top: 40px;
  }

}